import React, { Component } from "react";
import { Link } from "react-router-dom";
import Pagination from "../../components/Pagination";

export default function HomePage() {
  return (
    <div className="container-fluid" style={{}}>
   
      <div style={{ display: "flex", justifyContent: "space-between" }}>
        <h4 className="h4 title_content mb-0 text-gray-800">
          Cài đặt phân quyền
        </h4>{" "}
        <Link
          className="btn btn-info btn-icon-split btn-sm show"
          to="/decentralization/create"
        >
          <span className="icon text-white-50">
            <i className="fas fa-plus" />
          </span>
          <span className="text">Thêm phân quyền</span>
        </Link>
      </div>
      <div className="card shadow mb-4">
        <div className="card-header py-3">
     
          <div className="row" style={{ justifyContent: "space-between" }}>
            <form>
              <div className="input-group mb-6" style={{ padding: "0px 20px" }}>
                <input
                  type="search"
                  name="txtSearch"
                  className="form-control"
                  placeholder="Tìm theo khóa học"
                  
                  style={{ maxWidth: "400px", minWidth: "300px" }}
                />
                <div className="input-group-append">
                  <button className="btn btn-primary" type="submit">
                    <i className="fa fa-search" />
                  </button>
                </div>
              </div>
            </form>
       
          </div>
        </div>
        <div className="card-body">
          <div className="table-responsive">
            <div
              id="dataTable_wrapper"
              className="dataTables_wrapper no-footer"
            >
     
           
              <table
                className="table table-border no-footer dataTable"
                id="dataTable"
                width="100%"
                cellSpacing={0}
                aria-describedby="dataTable_info"
                style={{ width: "100%" }}
              >
                <thead>
                  <tr>
                    <th
                      className="sorting_disabled sorting_asc"
                      rowSpan={1}
                      colSpan={1}
                      style={{ width: "146.828px" }}
                      aria-label="STT"
                    >
                      STT
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      style={{ width: "283.5px" }}
                      aria-label="Tên nhóm"
                    >
                      Tên nhóm
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      style={{ width: "181px" }}
                      aria-label="Mô tả"
                    >
                      Mô tả
                    </th>
                    <th
                      className="sorting_disabled"
                      rowSpan={1}
                      colSpan={1}
                      style={{ width: "304.656px" }}
                      aria-label="Hành động"
                    >
                      Hành động
                    </th>
                  </tr>
                </thead>
                <tbody>
                  <tr className="odd">
                    <td className="sorting_1">1</td>
                    <td style = {{color : "blue"}}>Quản lý</td>
                    <td>Quản lý cấp cao</td>
                    <td>
                      {/* <Link
                        className="btn btn-warning btn-sm show"
                        to="/decentralization/edit"
                        style={{ marginLeft: "10px" }}
                      >
                        <i className="fa fa-edit" /> Sửa
                      </Link> */}
                      <button
                        data-toggle="modal"
                        data-target="#removeModal"
                        className="btn btn-danger btn-sm show"
                        style={{ marginLeft: "10px" }}
                      >
                        <i className="fa fa-trash" /> Xóa
                      </button>
                    </td>
                  </tr>
            
                </tbody>
              </table>
              <Pagination></Pagination>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
